/** @jsx jsx */
import { jsx, css } from '@emotion/react'
import { useItemsState, useItemsDispatch } from '../../items/_state/hooks'
import { useStorefrontState, useStorefrontDispatch } from '../_state/hooks'

function StorefrontSelectionsClear() {
	const itemsState = useItemsState()
	const itemsDispatch = useItemsDispatch()
	const storefrontDispatch = useStorefrontDispatch()
	const storefrontState = useStorefrontState()

	function clearAllSelections() {
		storefrontDispatch({ type: 'CLEAR_SELECTIONS' })
		itemsDispatch({ type: 'SET_QUERY_TYPE', payload: 'products' })

		itemsDispatch({
			type: 'SET_QUERY_PARAMS',
			payload: itemsState.originalParams,
		})

		itemsDispatch({ type: 'SET_IS_FETCHING_NEW', payload: true })
	}

	const clearAllCSS = css`
		text-decoration: underline;
		font-size: 14px;
		margin-left: 4px;
		margin-top: 0px;
		position: relative;
		top: -2px;

		&:hover {
			opacity: 0.8;
			cursor: pointer;
		}
	`

	return (
		<div
			className='wps-filter-selections-clear'
			onClick={clearAllSelections}
			css={clearAllCSS}>
			{itemsState.settings.clearFilterSelectionsText}
		</div>
	)
}

export { StorefrontSelectionsClear }
